import React, { Component } from 'react';
import { NavigationContainer, NavItem, NavItemEmbed, NavItemA } from './NavigationBar.style';

class NavigationBar extends Component {
  render() {
    return (
      <NavigationContainer
        row
        justify={'flex-end'}
        alignItems={'center'}
      >
        <NavItem {...this.props.scrollConfig} to='experience'>Experience</NavItem>
        <NavItem {...this.props.scrollConfig} to='projects'>Projects</NavItem>
        <NavItem {...this.props.scrollConfig} to='contact'>Contact</NavItem>
        <NavItemA target='_blank' href={'https://github.com/Jun711/react-portfolio/blob/gh-pages/JunResume.pdf'}>Resume</NavItemA>
        <NavItemA target='_blank' href={'http://www.language-diary.com/'}>Blog</NavItemA>
      </NavigationContainer>
    );
  }
}

export default NavigationBar



