import React, { Component } from 'react';
import { ProjectContainer, ProjectItem, ProjectWrapper, ProjectCover } from './ProjectGrid.style';
import Project from '../Project/Project';
import { projects } from './Projects.data'
import { Events, scrollSpy } from 'react-scroll';

class ProjectGrid extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showProject: true
    }
  }

  toggleProject(openProject) {
    if (openProject) {
      document.querySelector('body').style.overflow = 'hidden';
    } else {
      document.querySelector('body').style.overflow = 'initial';
    }
    this.setState({
      showProject: openProject
    })
  }

  renderProjects() {
    const projectItems = projects.map((project) =>
      <ProjectItem
        hoverContent={'abc'}
        data-content={project.languages}
        onClick={() => this.toggleProject.call(this, true)}
      ><ProjectCover src={require('../../assets/img/axis.jpeg')}/></ProjectItem>
    )

    return projectItems
  }

  // renderLanguages() {
  //   const languageItems = languages.map((language) =>
  //     <ListItem key={language.key}>{language.lang}</ListItem>
  //   )
  //   return (
  //     <ListGrid>
  //       {languageItems}
  //     </ListGrid>
  //   );
  // }

  render() {
    return (
      <ProjectWrapper>
        <h1>Projects</h1>
        <hr/>
        <ProjectContainer
          row
          justify={'center'}
          align={'center'}
        >
          <Project
            projectSwitch={this.state.showProject}
            closeProject={this.toggleProject.bind(this)}
            project={projects[0]}
          />
          {this.renderProjects()}
          <ProjectItem
            onClick={() => this.toggleProject.call(this, true)}
          ><ProjectCover src={require('../../assets/adblocker.png')}/></ProjectItem>
          <ProjectItem>456</ProjectItem>
          <ProjectItem>abc</ProjectItem>
          <ProjectItem>def</ProjectItem>
          <ProjectItem>ghi</ProjectItem>
        </ProjectContainer>
      </ProjectWrapper>
    );
  }
}

export default ProjectGrid;


