import React, { Component } from 'react';

import { ProjectContainer, Content, CloseButtonContainer, Image, ButtonFlex, ButtonDiv, ProjectContainerR } from './Project.style'
import FontAwButton from '../FontAwButton/FontAwButton'


class Project extends Component {
  _renderCloseButton() {
    return (
      <ButtonFlex
        row
        justify={'center'}
        alignItems={'center'}
      >
        <ButtonDiv>
          <i class='fa fa-times' aria-hidden='true'></i>
        </ButtonDiv>
      </ButtonFlex>
    )
  }

  render() {
    const projectSwitch = this.props.projectSwitch;
    return (
      <ProjectContainer
        hide={!projectSwitch}
      >
        <Content>
        {/*<Image src={require('../../assets/close.svg')} />*/}
          {/*{this._renderCloseButton()}*/}
        <CloseButtonContainer onClick={() => this.props.closeProject(false)}>
          <FontAwButton
                        class={'fa fa-times'}/>
        </CloseButtonContainer>
        <h1>{this.props.project.title}</h1>
        <FontAwButton class={'fa fa-angle-double-up'}/>
        </Content>
      </ProjectContainer>
    );
  }
}

export default Project;



