export const ButtonFlex = styled(Flex)`
  height: 4em;
  width: 4em;
  background-color: ${blue};
  color: ${orange};
   border-radius: 50%;
  //  border: 1px solid #fff;
  // i {
  //   font-size: 2em;
  //   background-color: ${yellow};
  //  }
`;

export const ButtonDiv = styled.div`
   display: inline-block;
 
   // background-color: ${red};
   height: 2em;
   width: 2em;
   // line-height: 4.5em;
   color: ${orange};
   // border-radius: 50%;
   // border: 1px solid #fff;
   text-align: center;
   vertical-align: middle;
   
   
   transition: background-color .3s ease-in-out;
   
   &:hover {
    background-color: ${teal};
    color: ${blue};
   }
   
   i {
   
    font-size: 2em;
    // background-color: ${yellow};
   }
`;


export const Image = styled.img`
  background-color: ${red};
  height: 3em;
  width: 3em;
  
  &:hover {
    color: ${blue};
    &:before {
      left: 1px;
      opacity: 1;
    }
    &:after {
      content: ' ';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: ${white};
      z-index: -1; 
      transform: scale(1.2, 1.7);
    }
  }
`

export const ProjectContainer = styled.div`
  color: ${blue};
  background-color: ${orange};
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 1;
  z-index: 100;
  text-align: center;
  h1 {
    margin: 0;
    margin-top: 25px;
  }

  transition: transform 0.7s ease-in-out, opacity 1s ease-in-out;
  
  ${({hide}) => hide && css`
    opacity: 0;
    transform: translateX(100%);
  `}
`;

export const ProjectContainerR = styled(Div)`
  color: ${blue};
  top: 7vh;
  background-color: ${green};
  position: fixed;
  width: 100vw;
  min-height: 100vh;
  top: 0;
  left: 0;
  opacity: 1;
  z-index: 1000;
  text-align: center;
  h1 {
    margin: 0;
    margin-top: 25px;
  }

  transition: transform 0.7s ease-in-out, opacity 1s linear;
  
  ${({hide}) => hide && css`
    opacity: 0;
    transform: translateY(-100%);
  `}
`;

export const Content = styled(Relative)`
  background-color: ${red};
  // width: 0vw;
  height: 100vh;
  overflow: scroll;
  // margin: 0 auto;
  padding-top: 7em;
`;

export const CloseButtonContainer = styled.div`
  position: absolute;
  background-color: ${yellow};
  // width: 80vw;
  // height: 80vh;
  top: 25px;
  right: 25px;
`;




